import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Timeline } from 'react-twitter-widgets'

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class DashTwitterWidget extends Component {
    render() {
        const { id, setProps, value } = this.props;

        return (
            <div id={id}>
                <Timeline
                    dataSource={{ sourceType: "profile", screenName: value }}
                    options={{ width: "500", height: "500" }}
                />
            </div>
        );
    }
}

DashTwitterWidget.defaultProps = {
    value: "Twitter"
};

DashTwitterWidget.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The value displayed in the input.
     */
    value: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
